@import url('https://fonts.googleapis.com/css2?family=Alata&family=Bebas+Neue&family=Crimson+Text&family=Dancing+Script:wght@700&family=Josefin+Sans&family=Montserrat:wght@300&family=Poppins&display=swap');


.experienceContainer
{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    align-items:center;
    margin: 200px 0;
}
.experienceContainer > h1
{
    padding-bottom: 20px;
}

.experienceContainer__info
{
    background: url(../TopContainer/bgg.jpg);
    background-position: center;
    background-size: cover;
    height: 300px;
    z-index: 1000px;
    width: 80vw;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 10px;
}