@import url('https://fonts.googleapis.com/css2?family=Alata&family=Bebas+Neue&family=Crimson+Text&family=Dancing+Script:wght@700&family=Montserrat:wght@300&family=Poppins&display=swap');


.skillContainer
{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0;
}

.skillContainer__text
{
    color: white;
    flex: 0.6;
    text-align: left;
    max-width: 800px;
    margin-bottom: 20px;
    font-size: 30px;

}

.skillContainer__text >h2{
    font-size: 30px;    
}

.skillContainer_image
{
    flex: 0.4;
    text-align: center;
    margin-right: 100px;   
}

.skillContainer_image > img
{
    border-radius: 15px;
    object-fit: contain;
}

.skillContainer__skillset > h5
{
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    padding-bottom: 15px;
    padding-top: 15px;
}

.skillconatiner__slider > .MuiLinearProgress-colorPrimary
{

    background-color: grey;
}

.skillcontainer__Slider1 > span >.css-5xe99f-MuiLinearProgress-bar1 
{
    background-color: rgb(36, 163, 100);
}   


.skillcontainer__Slider2 > span >.css-5xe99f-MuiLinearProgress-bar1 
{
    background-color: red;
}   



.skillcontainer__Slider3 > span >.css-5xe99f-MuiLinearProgress-bar1 
{
    background-color: tomato;
}       


.skillcontainer__Slider4 > span >.css-5xe99f-MuiLinearProgress-bar1 
{
    background-color: rgb(158, 18, 128);
}   


.skillcontainer__Slider5 > span >.css-5xe99f-MuiLinearProgress-bar1 
{
    background-color: rgb(171, 202, 31);
}   
