@import url('https://fonts.googleapis.com/css2?family=Alata&family=Bebas+Neue&family=Crimson+Text&family=Dancing+Script:wght@700&family=Montserrat:wght@300&family=Poppins&display=swap');

.topContent
{
    color: white;
    position: relative;
}

.topContent_conatiner
{
    position: absolute;
    top: 350px;
    right: 200px;
}
.topContent_conatiner > p{
    letter-spacing: 1px;
    text-align: left;
    font-size: 40px;
    padding-left: 3px;
    padding-top: 20px;
}

.topContent_conatiner > a > button
{    
       
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}       

.topContent_conatiner > h1 > span{
    color: rgb(219, 194, 54);
}
#devlop
{
    color: tomato;
}

.topContent_conatiner > h1{
    font-size: 70px;
    font-family: 'Montserrat', sans-serif;
}

.topContent_downloadbutton
{
    color: white;
    border: 1px solid white;    
    background-color: #f64c08;
    font-size: 14px;
    border-radius: 20px;
    padding: 10px 30px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px; 
}
.topContent_downloadbutton:hover
{
    color:#f64c08;
    background-color: white;
    font-weight: 600;
}

.topContent_workbutton
{
    border-radius: 20px;
    color: white;
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding: 9px 45px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    cursor: pointer;
}
.topContent_workbutton:hover
{
    color: black;
    background-color: white;
    font-weight: 600;

}