@import url('https://fonts.googleapis.com/css2?family=Alata&family=Bebas+Neue&family=Dancing+Script:wght@700&family=Montserrat:wght@300&family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alata&family=Bebas+Neue&family=Crimson+Text&family=Dancing+Script:wght@700&family=Montserrat:wght@300&family=Poppins&display=swap');

.header
{
    display: flex;
    align-items: center;
    color: white;
    background: black;
    top: 0;
    position: sticky;
    z-index: 2000;
}

.header--left
{
    flex: 0.4;
}
.header--left >h1{
    text-align: center;
    font-family: "Alata",sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}

.header--left > h1 >span{
    color: red;
    font-family: "Alata",sans-serif;
}

.header__right
{
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center ;
}

.header__right > a >h4{
    font-size: 18px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    padding: 20px 0;
    letter-spacing: 1px ;
    font-family: "Crimson Text",serif;
}

.header__right > a >h4:hover
{
    transform: scale(1.09);
    background-color: rgb(199, 67, 34);
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: "Crimson Text",serif;
    font-size: 18px;
    cursor: pointer;
}

.header__rightbutton
{
    background-color: rgb(199, 67, 34);
    color: white;
    padding: 5px 20px !important;
    border-radius: 7px;
    cursor: pointer;

}
